================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; position: relative; z-index: 0; }

body.hid { overflow: hidden; }

.t-show { display: none; }

/* */
form:after { content: ''; display: block; clear: both; }

.btn { letter-spacing: 1px; font-family: "Open Sans", sans-serif; transition: all 0.3s linear; display: inline-block; align-items: center; justify-content: center; border: 1px solid transparent; position: relative; z-index: 1; overflow: hidden; text-decoration: none; backface-visibility: hidden; -moz-osx-font-smoothing: grayscale; text-align: center; background: url(../img/bg-btn2.png) no-repeat center; background-size: 100% 100%; font-size: 18px; height: 74px; line-height: 44px; padding: 15px 20px; color: #d4cdbd; border-radius: 0px; font-weight: bold; }

.btn:hover { opacity: 0.8; color: #fff; }

.btn span { font-weight: 500; }

.btn [class^="icon-"] { font-weight: 400; }

.btn-gray { background-image: url(../img/bg-btn.png); }

.form_row { margin-bottom: 6px; }

.form_row_cols { display: flex; flex-direction: row; margin: 0 -8px; }

.form_col { width: 50%; padding: 0 8px; }

.form_group { padding: 11px 0; border-top: 1px solid #edf0f7; }

.form_group:first-child { border: none; }

.form_label { display: block; padding-left: 12px; margin-bottom: 3px; font-size: 12px; font-weight: 300; color: #d2cbbb; }

.form-control { position: relative; padding: 7px 12px; text-overflow: ellipsis; overflow: hidden; display: block; width: 100%; height: 37px; font-size: 12px; letter-spacing: 0.8px; color: #d2cbbb; line-height: 21px; font-family: "Open Sans", sans-serif; background: transparent; border: none; outline: none; }

.form-control-container { border-radius: 4px; position: relative; border: 1px solid #8b2618; }

.form-control-container:before { display: block; content: ''; border-radius: 4px; position: absolute; top: -3px; left: -3px; bottom: 2px; right: 2px; border: 1px solid rgba(91, 86, 79, 0.5); }

.form-control.has-error { color: red; border-color: red; }

.form-control-gr { background: #50586e; color: #fff; }

textarea.form-control { height: 112px; resize: none; }

.form-control::-webkit-input-placeholder { font-weight: 400; color: #d2cbbb; }

.form-control:-moz-placeholder { font-weight: 400; color: #d2cbbb; }

.form-control::-moz-placeholder { font-weight: 400; color: #d2cbbb; }

.form-control:-ms-input-placeholder { font-weight: 400; color: #d2cbbb; }

.checkbox { font-weight: 300; text-align: left; }

.checkbox label { display: inline-block; min-width: 15px; min-height: 15px; position: relative; cursor: pointer; }

.checkbox label input[type="checkbox"], .checkbox label input[type="radio"] { position: absolute; opacity: 0; }

.checkbox label input:checked ~ .checkbox_txt:after { opacity: 1; }

.checkbox_txt { position: relative; display: block; line-height: 15px; letter-spacing: 0.9px; padding-left: 30px; }

.checkbox_txt:before { content: ''; position: absolute; left: 0; top: 0; height: 15px; width: 15px; border-radius: 4px; background: #fff; z-index: 0; box-shadow: 0px 0px 6px rgba(0, 47, 76, 0.13); }

.checkbox_txt:after { content: ""; width: 15px; height: 12px; opacity: 0; position: absolute; top: 2px; left: 1px; background: url(../img/check.png) no-repeat 0 0; }

@keyframes checkbox-check { 0% { width: 0;
    height: 0;
    border-color: #f95c39;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  33% { width: 8px;
    height: 0;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  100% { width: 8px;
    height: 12px;
    border-color: #f95c39;
    transform: translate3d(0, -0.5em, 0) rotate(45deg); } }

.checkbox_inp { width: 22px; min-width: 22px; height: 15px; margin-right: 13px; padding: 0; line-height: 15px; text-align: center; font-weight: 300; font-size: 12px; }

